import React,
      { memo,
        useState,
        useEffect,
        useRef,
        useLayoutEffect,
        useImperativeHandle,
        forwardRef,
        Fragment }                 from 'react'

import { useSelector,
         useDispatch,
         shallowEqual,}            from 'react-redux'

import { APP_CONSTANTS,
         APP_VARS, }               from '..'

import { _Date,
         TIME_SLOTS, }             from "JS/_Date"

import { usePrevious,
         useIntersect,
         useBus,
         evDispatch, }             from 'Hooks'


import { store,
         tbodyInViewUPD,
         bookingsDayDataUPD,
         bTableDateUPD,
         testActionMD,
         tbInViewUpd_ThunkCreator,
         SELECTORS, }              from 'JS/store.js'





const StopwatchSVG = <svg width="14" height="14" viewBox="0 0 16 16">
    <path d="M8 3.019v-1.019h2v-1c0-0.552-0.448-1-1-1h-3c-0.552 0-1 0.448-1 1v1h2v1.019c-3.356 0.255-6 3.059-6 6.481 0 3.59 2.91 6.5 6.5 6.5s6.5-2.91 6.5-6.5c0-3.422-2.644-6.226-6-6.481zM11.036 13.036c-0.944 0.944-2.2 1.464-3.536 1.464s-2.591-0.52-3.536-1.464c-0.944-0.944-1.464-2.2-1.464-3.536s0.52-2.591 1.464-3.536c0.907-0.907 2.101-1.422 3.377-1.462l-0.339 4.907c-0.029 0.411 0.195 0.591 0.497 0.591s0.527-0.18 0.497-0.591l-0.339-4.907c1.276 0.040 2.47 0.555 3.377 1.462 0.944 0.944 1.464 2.2 1.464 3.536s-0.52 2.591-1.464 3.536z">
    </path></svg>

const ADM_HEADER         = true

const BT_HEADER_USER     = [ 'Time', StopwatchSVG, 'tarif', 'Flyers', 'Notes', 'Booking №', 'paid' ]
const BT_HEADER_ADM      = [ 'mail','phone']
const BT_HEADER_ARR      = ADM_HEADER ? BT_HEADER_USER.concat(BT_HEADER_ADM) : BT_HEADER_USER
const BT_HEADER_COLS_ARR = BT_HEADER_ARR.slice(1).map( (item, i) =>
    <th key={i} className = "th-header">
        {item}
    </th>
)
const COLS_CNT           = BT_HEADER_ARR.length

const BT_DATES_OFFSET    = APP_CONSTANTS.BT_DATES_OFFSET

const BT_BLANK_TBODY     = () => (
    <tbody className = "bt-serv-tbody">
        <tr>
            <td colSpan={COLS_CNT} className = "bt-serv-td">
                <br /><br /><br /> NO DATA <br />
            </td>
        </tr>
    </tbody>
)

const BT_EVENTS = {
    '@@ui/NAV_BTN_CLICK': (curDateN) => (e) => {
        scrollToDay(curDateN, e.payload)
    },

    '@@ui/TEST_BTN':
        (dispatch, tableDateN, endOffset) => () => {
         dispatch( bTableDateUPD(tableDateN+endOffset))
    },

    '@@ui/TEST_BTN2':
        (dispatch, newDateN) => () => {
         dispatch( tbInViewUpd_ThunkCreator(newDateN))
    },
}

const useJumpToSidebarViewTime = (sidebar_viewTime) => {
    useEffect(() => {
        if (!sidebar_viewTime) return

        const tableDateN       = SELECTORS.bTableDateN( store.getState() )
        const jumpDate         = _Date.dateMsToDateN(sidebar_viewTime)
        const isCurDateInTable = (jumpDate >= tableDateN + BT_DATES_OFFSET.stOffset)
                              && (jumpDate <= tableDateN + BT_DATES_OFFSET.endOffset)

        if ( !isCurDateInTable ) {
            store.dispatch( bTableDateUPD(jumpDate) )
        }
        const targetNode = scrollToTime(sidebar_viewTime)
        targetNode?.classList?.add('bt-sidebar_viewTime')

        return () => {targetNode?.classList?.remove('bt-sidebar_viewTime')}
    }, [sidebar_viewTime,])
}





export function BookingsTable() {
    const {stOffset, endOffset} = BT_DATES_OFFSET

    const dispatch         = useDispatch()
    const sidebar_viewTime = useSelector(SELECTORS.sidebar_viewTime)
    const tableDateN       = useSelector(SELECTORS.bTableDateN)
    const reqDateStr       = useSelector(SELECTORS.period, shallowEqual).reqDate
    const recDateMs   = new Date(reqDateStr).getTime() || Date.now()
    const reqDateN    = _Date.dateMsToDateN(recDateMs)

    const dateNsArr   = Array.from( {length: 1 + endOffset - stOffset},
                            ( _, i) => +tableDateN + (stOffset + i)
                        )

    useEffect(() => {
        dispatch( bTableDateUPD(reqDateN) )
        scrollToDay(reqDateN)
    }, [reqDateN, dispatch])

    useJumpToSidebarViewTime(sidebar_viewTime)


    return  <>
        <DayInViewDiv />
        <table id="mainTable" className="bookingsTable">
            <BookingsTableHeader />
            <TbodyesSet dateNsArr = {dateNsArr}/>
        </table>
    </>
}



function DayInViewDiv() {
    const dispatch      = useDispatch()
    const curTbodyInfo  = useSelector(SELECTORS.curTbodyInfo)
    const curDateN      = curTbodyInfo.dateN
    const testOnClik    = () => dispatch( testActionMD( 'HEAD DIV Click ' +Date.now() ) )

    useBus(
        '@@ui/NAV_BTN_CLICK',
         BT_EVENTS['@@ui/NAV_BTN_CLICK'](curDateN),
        [curDateN]
    )



    return (
        <div
            id        = 'curDayDiv'
            className = 'curDayDiv'
            onClick   = { testOnClik }
        >
            {curTbodyInfo.headerStr}
        </div>
    )
}
function BookingsTableHeader () {
    return (
        <tbody id="btHeaderTbody">
            <tr id="btHeaderTr" className="tableHeader">
                <th id="th0" className="th-header th0" >
                    {BT_HEADER_ARR[0]}
                </th>
                {BT_HEADER_COLS_ARR}
            </tr>
        </tbody>
    )
}


function TbodyesSet({dateNsArr}) {
    const bookingDataObj = useSelector(SELECTORS.bookingsObjDateNs(dateNsArr), shallowEqual)
    let   instrRequests  = useSelector(SELECTORS.instrRequests)
    // //debugger
    //  console.log(instrRequests)
    instrRequests = instrRequests
        ? new Set(
            Object.values( instrRequests).map(instrRequest => instrRequest.timeFlight )
          )
        : null


    let   needDataFlag   = false
    const tbodiesArr = dateNsArr.map( (curDateN, i) => {
        if ( bookingDataObj[curDateN]) {
            return <DateTbody
                key           = {curDateN}
                dateN         = {curDateN}
                dayDataObj    = {bookingDataObj[curDateN]}
                instrRequests = {instrRequests}
            />
        } else {
            needDataFlag = true
            return <BT_BLANK_TBODY key = {curDateN}/>
        }
    })

    if (needDataFlag) {//debugger
     //console.log('need Data', dateNsArr[0])
    }

    return tbodiesArr
}

const    TbodyHeaderOnClik = () => evDispatch(
    { type   : '@@ui/TBHEADER_CLICK',
      payload: ('header Click ' + Date.now() ) }
)
function TbodyHeader({dateObj, headerStr, colsCnt}) {

    return (
        <tr id        = {dateObj.dateN + '_0' }
            className = {dateObj.HDay ? 'newDayTr HDay' : 'newDayTr'}
        >
            <th>
                {dateObj.dayName},
                {dateObj.dayN} / {dateObj.monthN}
            </th>
            <td></td>
            <td colSpan = {colsCnt-1}
                onClick = {TbodyHeaderOnClik}
            >
                {headerStr}
            </td>
        </tr>
    )
}

function DateTbody({dateN, dayDataObj, instrRequests}) {
    const dispatch                 = useDispatch()
    const ADMIN_TOGGLE             = useSelector(SELECTORS.userObj).role === 'admin'
    const { colsCnt, curDateObj,
            headerStr,
            tbodyId, rowsJSXArr }  = getDateTbodyVals({dayDataObj, dateN, ADMIN_TOGGLE, instrRequests})

    const { intersectFunc,
            intersectOptions }     = setTBodyIntersect({ tbodyData : {headerStr, dateN},
                                                         dispatcher: dispatch })

    const targetRef                = useIntersect(intersectFunc, intersectOptions)



    return (
        <tbody
            ref       = {targetRef}
            id        = {tbodyId}
            className = "bookingsTable"
        >
            <TbodyHeader
                dateObj   = {curDateObj}
                headerStr = {headerStr}
                colsCnt   = {colsCnt}
            />
            {rowsJSXArr}
        </tbody>
    )
}




function getDateTbodyVals({dayDataObj, dateN, ADMIN_TOGGLE, instrRequests}) {
    const rowsJSXArr          = []
    const {timeCI,
           timeValCI,
           bookingIdCI,}      = APP_CONSTANTS.bookingsArrMap
    const restStCI            = timeValCI + 1
    const colsCnt             = COLS_CNT
    const minFreeTime         = 2

    const nowTime             = Date.now() - _Date.GMToffset
    const curDateObj          = _Date.msToCustomDateObj(dateN *_Date.hr24)

    const tbodyId             = dateN
    const headerStr           = getHeaderStrForDay(curDateObj)



    for (const tSlot in dayDataObj) {
        let curSlotTrJSXarr = []
        let curTSlotN       = Number(tSlot)
        let curTime         = curDateObj.timeMS + dayDataObj[tSlot][timeCI]
        let instrReq        = instrRequests?.has(curTime)

        let curRowsDataArr  = dayDataObj[tSlot][restStCI]
        let curTimeStr      = _Date.msToCustomDateObj( curTime ).timeStr

        let freeTime     = 30 - dayDataObj[tSlot][timeValCI]
        let rowSpan      = curRowsDataArr.length
                         + Number( freeTime >= minFreeTime && freeTime < 30 )

        let curTr0Id     = `${dateN}_${curTimeStr}`
        let trClassStr   = getTrClassList({
                                freeTime, minFreeTime,
                                timeSlotN: curTSlotN,
                                curTime, nowTime,
                                instrReq, })

        let curThJSX     = doThJSX(rowSpan, curTimeStr)



        let subTd0_JSXArr  = (freeTime >= 30) ?
                                [doFreetimeTdJSX(colsCnt)]
                               : doSubTdJSXArr(curRowsDataArr[0])


        let curTr0JSX   =   <tr id={curTr0Id} className={trClassStr} key={curTr0Id}>
                                {curThJSX}{subTd0_JSXArr}
                            </tr>


        curSlotTrJSXarr.push(curTr0JSX)

        if (rowSpan > 1) {
            let subStrArr   = doSubTrJSXArr(tSlot, trClassStr, curRowsDataArr.slice(1))
            curSlotTrJSXarr.push(subStrArr)
        }

        if(freeTime >= minFreeTime && freeTime < 30) {
            curSlotTrJSXarr.push(
                <tr className={trClassStr} key={"adTr" +tSlot} >
                    { doFreetimeTdJSX(colsCnt, freeTime) }
                </tr>
            )
        }

        rowsJSXArr.push(...curSlotTrJSXarr)
    }//end do Tr for tSlots

    return {colsCnt, curDateObj, tbodyId, headerStr, rowsJSXArr}



        function doThJSX(rowSpan, timeStr) {
            let ThJSX = <th rowSpan={rowSpan} >
                            {timeStr}
                            <br/>
                            <span className="tdSpan"></span>
                        </th>
            return ThJSX
        }//end sub for Th
        function doFreetimeTdJSX (colsCnt, freeTime) {
            const freeTimeText = `-- свободно ${freeTime ? freeTime + ' мин.' : ''} --`
            return (
                <td key="ft" colSpan={colsCnt} className="freeTimeTd" >
                    <ADDREC_BTN freeTimeText = {freeTimeText}/>
                </td>
            )
        }//end sub fot vacantTd


        function doSubTdJSXArr(rowDataArr) {
            const bookingId = rowDataArr[bookingIdCI-1]

            let tdJSXArr = rowDataArr
                .slice(0, colsCnt-1)
                .map(
                    (element, i) => (
                        <td key={'pf'+i} className="pf">
                            <pre>{element}</pre>
                        </td>
                    )
            )
            if (ADMIN_TOGGLE && bookingId) {
                tdJSXArr[bookingIdCI-1] = (
                    <td key={'pf'+bookingIdCI-1} className="pf">
                        <EDITREC_BTN bookingId = {bookingId}/>
                    </td>
                )
            }
            return tdJSXArr
        }
        function doSubTrJSXArr(tSlot, className, subDataArr) {
            let TrJSXArr = subDataArr.map(
                (rowDataArr, i) => (
                    <tr
                        key       = {tSlot + '_' + i}
                        className = {className}
                    >
                        {doSubTdJSXArr(rowDataArr)}
                    </tr>
                )
            )
            return TrJSXArr
        }//end sub fot Td



}// END dayObj to TbodyJSX

function getHeaderStrForDay(dateObj) {
    let dateStr   = _Date.dateToLongStrRu(dateObj.date)
    let headerStr = `${dateStr} ${dateObj.HDay ? ' (вых)':' (будень)'}`
    return headerStr
}

function getTrClassList({freeTime, minFreeTime, timeSlotN, curTime, nowTime, instrReq}) {
    const dayNight  = (timeSlotN <= 17)         ? 'groupN '      : 'groupD '
    const oddity    = (timeSlotN % 2)           ? 'odd '         : 'even '
    const noTime    = (freeTime <= minFreeTime) ? 'noTime-book ' : ''
    const pastTime  = (curTime < nowTime )      ? 'pastSlot '    : ''
          instrReq  = (instrReq)                ? 'instrReq'     : ''


    return dayNight + oddity + noTime + pastTime + instrReq
}









function scrollToDay(curDateN, direction = 'cur') {
    const scrollOptions = {
        block: "center",
    }
    const targetDateN = {
        cur:   curDateN,
        prev: +curDateN - 1,
        next: +curDateN + 1,
    }
    const parentNode = document.getElementById('mainTable')
    if (!parentNode) return

    const targetId   = targetDateN[direction]
    const targetNode = document.getElementById(targetId)
    if (!targetNode) {
        const observer = observeToScroll(parentNode, targetId, scrollOptions)
        setTimeout(() => {
            observer.disconnect()
        }, 5000)
        return
    }
    console.dir(`scroll to ${targetDateN[direction]} (${targetNode.id})`)
    targetNode.scrollIntoView(scrollOptions)
}

function scrollToTime(time) {
    const scrollOptions = {
        block: "center",
    }

    const containerNode = document.getElementById('inner')
    const parentNode    = document.getElementById('mainTable')
    if (!parentNode || !containerNode) return

    const reqDateN    = _Date.dateMsToDateN(time)
    const reqTimeSlot = TIME_SLOTS[ _Date.msToSlotN(time) ]
    const targetId    = `${ reqDateN }_${ reqTimeSlot[2] }`
    const targetNode  = document.getElementById(targetId)




    if (!targetNode) {
        const observer = observeToScroll(parentNode, targetId, scrollOptions)
        setTimeout(() => {
            observer.disconnect()
        }, 5000)
        return
    } else {
        const targetPosition    = targetNode.getBoundingClientRect()
        const containerPosition = containerNode.getBoundingClientRect()
        if (    targetPosition.top - containerPosition.top       > 70
             && containerPosition.bottom - targetPosition.bottom > 10 ) { return targetNode }
    }

    console.dir(`scroll to (${targetId})`)
    targetNode.scrollIntoView(scrollOptions)
    return targetNode
}


function observeToScroll(parentNode, targetId, scrollOptions) {
    parentNode = parentNode || document.getElementById('mainTable')

    const [tbodyId, trPartId] = (targetId+'_').split('_')

    const action = (node, observer) => {
        observer.disconnect()
        node.scrollIntoView(scrollOptions)
    }

    const callback = (mutationsList, observer) => {
        for(const mutation of mutationsList) {
            if (mutation.type !== 'childList') { continue }

            for (let node of mutation.addedNodes) {
                if (node.id != tbodyId) { continue }
                if (trPartId) {
                    node = document.getElementById?.(targetId)
                }
                return action(node, observer)
            }
        }
    }

    const observer = new MutationObserver(callback)
          observer.takeRecords()
          observer.observe(parentNode, { childList: true })

    return observer
}

function setTBodyIntersect({tbodyData, dispatcher})  {
    const intersectOptions = {
        root:          null,
        rootMargin:    '-20% 0% -80% 0%',
    }

    const intersectFunc = (e) => {
        const {
            isIntersecting,
        } = e[0]

        if (!isIntersecting) return

        dispatcher(tbInViewUpd_ThunkCreator(tbodyData))
    }

    return {intersectOptions, intersectFunc}
}



function ADDREC_BTN({freeTimeText}) {
    let onClick = (e) => evDispatch({type: '@@ui/ADDREC_BTN', payload: e.target})
    return (
        <button
            className = "freeTimeButton"
            onClick   = {onClick}
        >
            {freeTimeText}
        </button>
    )
}

function EDITREC_BTN({bookingId}) {
    let onClick = (e) => evDispatch({type: '@@ui/EDIT_BOOKING_CALL', payload: e.target.innerText})
    return (
        <button
            className = "editRecButton"
            onClick   = {onClick}
        >
            {bookingId}
        </button>
    )
}



export default BookingsTable
